import React from 'react';
import {Route, Switch} from 'react-router-dom'
import Home from './components/pages/home-page';
import { BrowserRouter } from "react-router-dom";
// import MenuDay from './components/pages/menu_day_buffet';
import Admin from './page-admin-securitu/administrator-page';
import Furshet from '../src/components/pages/furshet-menu';
import {PUBLIC_URL} from '../src/page-admin-securitu/urls-firebase'

class App extends React.Component{

  render(){
    return(
      <BrowserRouter basename={PUBLIC_URL}>
        <div>
          <Switch>
            <Route path={PUBLIC_URL} exact component={Home} />
            {/* <MenuDay/> */}
            <Route path={`${PUBLIC_URL}/administrator`}  component={Admin} />
            <Route path={`${PUBLIC_URL}/furshet`} component={Furshet} />
          </Switch>
        </div>
      </BrowserRouter>
    )
  }
}

export default App;
